<template>
	<view class="detail">
		<view class="detail-title">
			{{formData.title}}
		</view>
		<view class="detail-header">
			<view class="detail-header-img">
				<image :src="formData.author.avatar" mode="aspectFill"></image>
			</view>
			<view class="detail-header-content">
				<view class="detail-header-cont-title">
					{{formData.author.author_name}}
				</view>
				<view class="detail-header-cont-info">
					<text>{{formData.create_time}}</text>
					<text>{{formData.browse_count}} 浏览</text>
					<text>{{formData.thumbs_up_count}} 赞</text>
				</view>
			</view>
		</view>
		<!-- 内容 -->
		<view class="detail-content">
			<!-- 内容区 -->
			<view class="detail-html">
				<!-- <u-parse :content="formData.content" :noData="noData"></u-parse> -->
			</view>
			<!-- 评论区 -->
			<view class="detail-comment">
				<view class="comment-title">
					最新评论
				</view>
				<view class="comment-content" v-for="item in commentsList" :key="item.comment_id">
					<comments-box :comments="item" @reply="reply"></comments-box>
				</view>
			</view>
		</view>
		<!-- 工具栏 -->
		<view class="detail-bottom">
			<view class="detail-bottom-input" @click="openComment">
				<text>谈谈你的看法</text>
				<uni-icons type="compose" size="16" color="#F07373"></uni-icons>
			</view>
			<view class="detail-bottom-icons">
				<view class="detail-bottom-icons-box">
					<uni-icons type="chat" size="22" color="#F07373"></uni-icons>
				</view>
				<view class="detail-bottom-icons-box">
					<uni-icons type="heart" size="22" color="#F07373"></uni-icons>
				</view>
				<view class="detail-bottom-icons-box">
					<uni-icons type="hand-thumbsup" size="22" color="#F07373"></uni-icons>
				</view>
			</view>
		</view>
		<!-- 发布评论弹框 -->
		<!-- 自定义组件可使用ref获取节点 -->
		<uni-popup type="bottom" ref="popup" :maskClick="false">
			<!-- 弹出层 -->
			<view class="popup-uwrap">
				<view class="popup-header">
					<text class="popup-header-item" @click="close">取消</text>
					<text class="popup-header-item" @click="submit">发布</text>
				</view>
				<view class="popup-content">
					<!-- textarea 是在一个 position:fixed 的区域，需要显示指定属性 fixed 为 tru -->
					<textarea v-model="commentsValue" placeholder="请输入评论内容" maxlength="200" fixed  class="popup-textarea" />
					<view class="popup-count">
						{{commentsValue.length}}/200
					</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	// 富文本
	import uParse from "@/components/gaoyia-parse/parse.vue"
	export default {
		data() {
			return {
				formData:{},
				noData:'<p style="text-align:center;color:#666;">详情加载中</p>',
				commentsValue:"",//输入评论内容
				commentsList:[],//评论数据
				replyFormData:{},//回复相关变量
			}
		},
		components:{uParse},
		onLoad(query) {
			// console.log(JSON.parse(query.params))
			// 数据预加载
			this.formData = JSON.parse(query.params)
			// 请求数据
			this.getArticleDetail()
			// 获取评论数据
			this.getComments()
		},
		onReady() {
			// 所有组件渲染完成后
			// popup
			// this.$refs.popup.open()
		},
		methods: {
			// 获取文章详情信息
			getArticleDetail(){
				this.$api.get_detail({
					article_id:this.formData._id
				}).then(res=>{
					// console.log(res)
					this.formData = res.data
				})
			},
			openComment(){
				this.$refs.popup.open()
			},
			close(){
				this.$refs.popup.close()
			},
			// 提交评论
			submit(){
				if(!this.commentsValue){
					uni.showToast({
						title:"请输入评论内容",
						icon:'none'
					})
					return
				}
				this.updateComment({
					content:this.commentsValue,//评论内容
					...this.replyFormData//回复信息
				})
	
			},
			// 更新评论
			updateComment(data){
				uni.showLoading()
				const formData = {
					article_id:this.formData._id,
					...data
				}
				this.$api.update_comment(formData).then(res=>{
					console.log(res)
					uni.hideLoading()
					this.close()
					uni.showToast({
						title:"发布成功"
					})
					this.getComments()
				}).catch(err=>{
					console.log(err)
					uni.hideLoading()
					this.close()
				})
			},
			// 请求评论数据
			getComments(){
				this.$api.get_comments({
					article_id:this.formData._id,
				}).then(res=>{
					console.log(res)
					const {data} = res
					this.commentsList = data
				}).catch(err=>{
					console.log(err)
				})
			},
			// 评论回复
			reply(e){
				this.replyFormData = {
					"comment_id":e.comment_id,//回复的评论id
				}
				
				this.openComment()
			}
		}
	}
</script>

<style lang="scss">
	.detail{
		padding:15px 0;
		padding-bottom: 54px;
	}
.detail-title{
	padding: 0 15px;
	font-size: 18px;
	font-weight: bold;
	color: #333;
}
.detail-header{
	display: flex;
	align-items: center;
	margin-top: 10px;
	padding: 0 15px;
	
	.detail-header-img{
		flex-shrink: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		overflow: hidden;
		
		image{
			width: 100%;
			height: 100%;
		}
	}

	.detail-header-content{
		padding-left: 10px;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		font-size: 12px;
		
		.detail-header-cont-title{
			margin-bottom: 5px;
			color: #333;
			font-size: 14px;
		}
		.detail-header-cont-info{
			color: #999;
			text{
				margin-right: 10px;
			}
		}
	}

}

.detail-content{
	margin-top: 20px;
	min-height: 500px;
	// border: 1px solid red;
	
	.detail-html{
		padding: 0 15px;
	}
	
	.detail-comment{
		margin-top: 30px;
		
		.comment-title{
			padding: 10px 15px;
			font-size: 14px;
			color: #666;
			border-bottom: 1px solid #F5F5F5;
		}
		.comment-content{
			padding: 0 15px;
			border-top: 1px solid #eee;
		}
	}
}

.detail-bottom{
	position: fixed;
	bottom:0;
	left: 0;
	display: flex;
	align-items: center;
	width: 100%;
	height: 44px;
	border-top: 1px solid #f5f5f5 ;
	background-color: #fff;
	box-sizing: border-box;
	
	.detail-bottom-input{
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-left: 10px;
		padding: 0 10px;
		width: 100%;
		height: 30px;
		border: 1px solid #ddd;
		border-radius: 5px;
		
		text{
			color: #999;
			font-size: 14px;
		}
	}
	
	.detail-bottom-icons{
		display: flex;
		flex-shrink: 0;
		padding: 0 10px;
		
		.detail-bottom-icons-box{
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 44px;
		}
	}
}

.popup-uwrap{
	background-color: #fff;
	
	.popup-header{
		display: flex;
		justify-content: space-between;
		font-size: 14px;
		color: #666;
		border-bottom: 1px solid #F5F5F5;
		
		.popup-header-item{
			height: 50px;
			line-height: 50px;
			padding: 0 15px;
		}
		
	}

	.popup-content{
		width: 100%;
		padding: 15px;
		box-sizing: border-box;
		
		.popup-textarea{
			width: 100%;
			height: 200px;
		}
		
		.popup-count{
			display: flex;
			justify-content: flex-end;
			font-size: 12px;
			color: #999;
		}
	}
	
}


</style>
